import React, { useState, useEffect, useRef } from 'react';
import styled from 'styled-components';
import { motion, AnimatePresence } from 'framer-motion';
import { Card } from '../../utils/cards';

interface InfoButtonProps {
  currentCard?: Card | null;
  isGamePage?: boolean;
}

const InfoIcon = styled.img`
  width: 50px;
  height: 50px;
  cursor: pointer;
  transition: transform 0.3s ease;
  border-radius: 10px;
  filter: brightness(1.2);

  &:hover {
    transform: scale(1.1);
  }
`;

const RulesContainer = styled.div`
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(0, 0, 0, 0.95);
  padding: 20px;
  border-radius: 15px;
  border: 2px solid #ffd700;
  color: #ffd700;
  max-width: 90%;
  max-height: 90vh;
  overflow-y: auto;
  z-index: 1000;
  font-family: 'MedievalSharp', cursive;
`;

const RuleText = styled.div`
  text-align: left;
  white-space: pre-wrap;
  font-family: 'MedievalSharp', cursive;
  font-size: 1.1em;
  line-height: 1.8;
  margin: 0 auto;
  max-width: 800px;
`;

const Overlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  z-index: 999;
`;

const InfoButton: React.FC<InfoButtonProps> = ({ currentCard, isGamePage = false }) => {
  const [showRules, setShowRules] = useState(false);
  const [showTooltip, setShowTooltip] = useState(false);
  const tooltipRef = useRef<HTMLDivElement>(null);

  const handleClickOutside = (event: MouseEvent) => {
    if (tooltipRef.current && !tooltipRef.current.contains(event.target as Node)) {
      setShowRules(false);
    }
  };

  useEffect(() => {
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  const toggleRules = () => {
    setShowRules(!showRules);
  };

  const getAllRules = () => {
    return `Rgles des cartes :
Cartes Cur et Carreau (Rouges)
Cartes numrotes de 1  5 : Le joueur boit le nombre de gorges indiqu.
Carte 6 :
Le joueur peut ajouter une nouvelle rgle temporaire (valable jusqu' ce qu'un autre joueur tire un 6).
Ou, il peut supprimer une rgle existante.
Carte 7 : Le joueur lance le jeu "Dans ma valise".
 tour de rle, chaque joueur ajoute un lment  la valise.
Option spciale : Le joueur peut dire "Rpte" pour dfier le dernier joueur.
Si le dernier joueur russit, celui qui a dit "Rpte" boit le double.
Si le dernier joueur choue, il boit une gorge.
Carte 8 : Le joueur doit dire une chose qu'il a dj faite mais que les autres n'ont jamais faite.
Ceux qui n'ont jamais fait cette chose boivent une gorge.
Si tout le monde l'a dj fait, le joueur boit autant de gorges que le nombre de joueurs.
Carte 9 : Le joueur choisit un thme (films, couleurs, pays, etc.).
Chaque joueur doit nommer un lment du thme. Celui qui choue boit une gorge.
Carte 10 : Le joueur dit une chose qu'il n'a jamais faite, mais que les autres ont peut-tre dj faite.
Ceux qui l'ont dj faite boivent une gorge. Si personne ne l'a faite, le joueur boit autant de gorges que le nombre de joueurs.

Cartes Trfle et Pique (Noires)
Cartes numrotes de 1  5 : Le joueur distribue le nombre de gorges indiqu  d'autres joueurs (il ne boit pas lui-mme).
Les cartes de 6  10 suivent les mmes rgles que les cartes rouges.

Cartes Figures (Valet, Dame, Roi)
Valet (toutes couleurs) : Tous les hommes boivent une gorge.
Dame (toutes couleurs) : Toutes les femmes boivent une gorge.
Roi (toutes couleurs) : Le joueur devient le Roi de la table.
Il peut placer son poing sous son menton  tout moment.
Les autres doivent suivre. Le dernier  le faire boit une gorge.`;
  };

  const getCurrentCardRule = () => {
    if (!currentCard) {
      return "Tirez une carte pour voir la rgle";
    }

    const rules: { [key: string]: string } = {
      'A': currentCard.suit === 'hearts' || currentCard.suit === 'diamonds' ?
        "Le joueur boit 1 gorge." :
        "Le joueur distribue 1 gorge  un autre joueur.",
      '2': currentCard.suit === 'hearts' || currentCard.suit === 'diamonds' ?
        "Le joueur boit 2 gorges." :
        "Le joueur distribue 2 gorges  d'autres joueurs (il ne boit pas lui-mme).",
      '3': currentCard.suit === 'hearts' || currentCard.suit === 'diamonds' ?
        "Le joueur boit 3 gorges." :
        "Le joueur distribue 3 gorges  d'autres joueurs (il ne boit pas lui-mme).",
      '4': currentCard.suit === 'hearts' || currentCard.suit === 'diamonds' ?
        "Le joueur boit 4 gorges." :
        "Le joueur distribue 4 gorges  d'autres joueurs (il ne boit pas lui-mme).",
      '5': currentCard.suit === 'hearts' || currentCard.suit === 'diamonds' ?
        "Le joueur boit 5 gorges." :
        "Le joueur distribue 5 gorges  d'autres joueurs (il ne boit pas lui-mme).",
      '6': "Le joueur peut ajouter une nouvelle rgle temporaire (valable jusqu' ce qu'un autre joueur tire un 6), ou il peut supprimer une rgle existante.",
      '7': "Le joueur lance le jeu \"Dans ma valise\".  tour de rle, chaque joueur ajoute un lment  la valise. Option spciale : Le joueur peut dire \"Rpte\" pour dfier le dernier joueur. Si le dernier joueur russit, celui qui a dit \"Rpte\" boit le double. Si le dernier joueur choue, il boit une gorge.",
      '8': "Le joueur doit dire une chose qu'il a dj faite mais que les autres n'ont jamais faite. Ceux qui n'ont jamais fait cette chose boivent une gorge. Si tout le monde l'a dj fait, le joueur boit autant de gorges que le nombre de joueurs.",
      '9': "Le joueur dit une chose qu'il n'a jamais faite, mais que les autres ont peut-tre dj faite. Ceux qui l'ont dj faite boivent une gorge. Si personne ne l'a faite, le joueur boit autant de gorges que le nombre de joueurs.",
      '10': "Le joueur choisit un thme (films, couleurs, pays, etc.). Chaque joueur doit nommer un lment du thme. Celui qui choue boit une gorge.",
      'J': "Tous les hommes boivent une gorge.",
      'Q': "Toutes les femmes boivent une gorge.",
      'K': "Le joueur devient le Roi de la table. Il peut placer son poing sous son menton  tout moment. Les autres doivent suivre. Le dernier  le faire boit une gorge."
    };

    return rules[currentCard.symbol] || "Rgle inconnue";
  };

  return (
    <div style={{ position: 'fixed', top: '20px', right: '20px', zIndex: 100 }}>
      <InfoIcon
        src={process.env.PUBLIC_URL + '/images/info-button.jpg'}
        alt="Info"
        onClick={toggleRules}
        onMouseEnter={() => setShowTooltip(true)}
        onMouseLeave={() => setShowTooltip(false)}
      />
      
      {showTooltip && !showRules && (
        <div style={{
          position: 'absolute',
          top: '100%',
          right: 0,
          background: 'rgba(0, 0, 0, 0.9)',
          color: '#ffd700',
          padding: '10px',
          borderRadius: '5px',
          whiteSpace: 'nowrap'
        }}>
          {isGamePage ? "Voir la rgle de la carte" : "Voir toutes les rgles"}
        </div>
      )}

      {showRules && (
        <>
          <Overlay onClick={() => setShowRules(false)} />
          <RulesContainer ref={tooltipRef}>
            <RuleText>
              {isGamePage ? getCurrentCardRule() : getAllRules()}
            </RuleText>
          </RulesContainer>
        </>
      )}
    </div>
  );
};

export default InfoButton;
